/* Scss Document */

.helpful * { box-sizing: border-box; }
.helpful .helpful-controls {
    display: flex;
    margin-top: 0 !important;
}
.helpful {
  color: #999;
  position: relative;
  padding: 15px;
  font-size: 1rem;
  background-color: $gray-100;
	-webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: center;
  justify-content: center;
border:none !important;	
}

@media all and (max-width: 992px) {
  .helpful {
    flex-direction: column;
  }
}

/* HEADING */

.helpful > .helpful-header {
  margin-right: 10px;
}

.helpful > .helpful-header > .helpful-headline {
  font-size: 1rem !important;
  line-height: 28px;
  color: #333;
  display: inline-block;
  margin: 0 !important;
}

/* CONTENT */

.helpful > .helpful-content {
	color: #999;
	font-size: 1rem;
	line-height: 1.6rem;
}

.helpful > .helpful-content > span {
  display: none;
}

.helpful > .helpful-content > a {
	color: #999;
	text-decoration: underline;
}

.helpful > .helpful-content > a:hover {
	color: #999;
	text-decoration: none;
}

/* CONTROLS */

.helpful > .helpful-controls {
  display: flex;
}

.helpful > .helpful-controls > div > .helpful-button {
margin-top: 0 !important;	
  margin: 0 !important;
  display: inline-block;
  vertical-align: middle;
  line-height: 38px;
  text-align: center;
  text-transform: uppercase;
  border-radius: 500px;
  background-color: transparent;
  color: #333;
  border: 1px solid #e5e5e5;
  padding: 0 15px;
  line-height: 28px;
  font-size: .875rem;
  cursor: pointer;
  position: relative;
  transition: all .2s ease-in-out;
}

.helpful > .helpful-controls > div:first-child {
  margin-right: 15px;
}

.helpful > .helpful-controls > div > .helpful-button:hover {
  background-color: #e5e5e5;
	transition: all .2s ease-in-out;
}

.helpful > .helpful-controls > div > .helpful-button > .helpful-counter {
	right: -7.5px;
  top: -7.5px;
	color: #f5f5f5;
  position: absolute;
  font-size: 0.75rem;
  line-height: 0.75rem;
	background-color: #333;
	padding: 2.5px 7px;
  border-radius: 15px;
	box-shadow: 0px 2px 3px 0px rgba(0, 0, 0, 0.1);
	z-index: 2;
}

/* CREDITS */

.helpful > .helpful-footer {
	color: #bbb;
	font-size: .7rem;
	position: absolute;
	text-align: center;
	bottom: 0;
  right: 15px;
}

.helpful > .helpful-footer > a {
  color: #bbb;
}

/* FEEDBACK */
@media all and (min-width: 992px) {
  .helpful > .helpful-content > .helpful-feedback-form {
    min-width: 320px;
  }
}

.helpful > .helpful-content > .helpful-feedback-form {
	color: #333;
}

.helpful > .helpful-content > .helpful-feedback-form req {
  color: #ed7161;
}

.helpful > .helpful-content > .helpful-feedback-form p {
  margin: 0 0 12.5px;
}

.helpful > .helpful-content > .helpful-feedback-form > p,
.helpful > .helpful-content > .helpful-feedback-form > .feedback-note > p,
.helpful > .helpful-content > .helpful-feedback-form > .group {
	color: #333;
  margin: 0 0 12.5px;
}

.helpful .helpful-feedback-form textarea,
.helpful .helpful-feedback-form input[type="text"],
.helpful .helpful-feedback-form input[type="email"],
.helpful .helpful-feedback-form input[type="url"],
.helpful .helpful-feedback-form input[type="number"] {
  color: #333;
  width: 100%;
  display: block;
	font-size: 1rem;
	line-height: 1.6rem;
	padding: 7.5px 15px;
	border-style: solid;
	border-width: 1px;
	border-color: #e5e5e5;
  background-color: #fff;
	border-radius: 3px;
	box-shadow: inset 0px 2px 5px 0px #f4f4f4;
	-moz-box-shadow: inset 0px 2px 5px 0px #f4f4f4;
	-webkit-box-shadow: inset 0px 2px 5px 0px #f4f4f4;
}

.helpful > .helpful-content > .helpful-feedback-form textarea {
	min-height: 150px;
	max-width: 100%;
	font-size: 1rem;
	line-height: 1.6rem;
}

.helpful .helpful-feedback-form input[type="checkbox"],
.helpful .helpful-feedback-form input[type="radio"] {
	border-style: solid;
	border-width: 1px;
	border-color: #e5e5e5;
  background-color: #fff;
	border-radius: 3px;
	font-size: 1rem;
	line-height: 1.6rem;
	box-shadow: inset 0px 2px 5px 0px #f4f4f4;
	-moz-box-shadow: inset 0px 2px 5px 0px #f4f4f4;
	-webkit-box-shadow: inset 0px 2px 5px 0px #f4f4f4;
}

.helpful .helpful-feedback-form .helpful-button {
  text-transform: none;
  display: inline-block;
  vertical-align: middle;
  line-height: 38px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  border-radius: 500px;
  background-color: #f8f8f8;
  color: #333;
  border: 1px solid #333;
	font-size: 1rem;
	line-height: 1.6rem;
	padding: 7.5px 15px;
	height: 56px;
	max-height: 56px;
  margin: 12.5px 0;
  cursor: pointer;
	transition: all .2s ease-in-out;
	-moz-transition: all .2s ease-in-out;
	-webkit-transition: all .2s ease-in-out;
}

.helpful .helpful-feedback-form .helpful-button:hover {
  background-color: #333;
  color: #f8f8f8;
  border: 1px solid #333;
	transition: all .2s ease-in-out;
	-moz-transition: all .2s ease-in-out;
	-webkit-transition: all .2s ease-in-out;
}

.helpful .helpful-controls > div > .helpful-button {
    padding: 0 11px;
    height: 39px;
    max-height: 39px;
    font-size: 1rem;
    line-height: 1rem;
    position: relative;
    cursor: pointer;
    opacity: 1;
    width: 100%;
    display: block;
    text-align: left;
    color: #fff;
    border: 0px;
    border-radius: 35px;
    transition: opacity .2s ease-in-out;
    -moz-transition: opacity .2s ease-in-out;
    -webkit-transition: opacity .2s ease-in-out;
    box-shadow: 0px 2px 5px 0px #f4f4f4;
    -moz-box-shadow: 0px 2px 5px 0px #f4f4f4;
    -webkit-box-shadow: 0px 2px 5px 0px #f4f4f4;
}
