code,
code[class*=language-],
pre,
pre[class*=language-] {
	direction: ltr;
	text-align: left;
	white-space: pre;
	word-spacing: normal;
	word-break: normal;
	-moz-tab-size: 4;
	-o-tab-size: 4;
	tab-size: 4;
	-webkit-hyphens: none;
	-ms-hyphens: none;
	hyphens: none
}

pre[class*=language-] {
	overflow: auto
}

.token.cdata,
.token.comment,
.token.doctype,
.token.prolog {
	color: gray
}

.token.punctuation {
	color: #999
}

.namespace {
	opacity: .7
}

.token.boolean,
.token.constant,
.token.deleted,
.token.number,
.token.property,
.token.symbol,
.token.tag {
	color: #905
}

.token.attr-name,
.token.builtin,
.token.char,
.token.inserted,
.token.selector,
.token.string {
	color: #690
}

.language-css .token.string,
.style .token.string,
.token.entity,
.token.operator,
.token.url {
	color: #a67f59
}

.token.atrule,
.token.attr-value,
.token.keyword {
	color: #07a
}

.token.function {
	color: #dd4a68
}

.token.important,
.token.regex,
.token.variable {
	color: #e90
}

.token.important {
	font-weight: 700
}

.token.entity {
	cursor: help
}